.header-container {
 
    display: flex;
   justify-content: center; 
   height: 149px;
 }

 .header-parent {
   display: flex;
   align-items: center;
   justify-content: space-around;
   height: 100%;
   width: 70%;
   color: white;
   font-weight: 600;
 }
 .header-logo {
   display: flex;
   justify-content: flex-start;
   font-size: 50px;
   font-family: "Poppins ExtraBold";
   color: white;
   position: relative;
   left: -70px;
 }
 .header-options {
   display: flex;
   align-items: center;
   cursor: pointer;

   
 }
 .header-option {
   font-size: 16px;
   cursor: pointer;

 }

 .header-hamburger {
   display: none;
   color: white;
 }
 .header-option-seperator {
   margin: 0 50px 0 0;
   color: white;
 }
  .selected-header-option {
    color: #9ba5e6; 
   
 } 
 .header-parent span{
   display: flex;
   flex-direction: row;
   padding-left: 50px;
    position: relative;
   left: 110px; 
   
 }

 @media only screen and (max-width: 1670px) {
   header-parent {
     width: 80%;
   }
   .header-parent span{

       cursor: pointer;
        position: relative;
       left: 70px; 
     }
 }

 /*IPAD PRO RESPONSIVENESS*/
 @media only screen and (max-width: 1110px) {
   header-parent {
     width: 80%;
   }
   .header-parent span{

       cursor: pointer;
        position: relative;
       left: 50px; 
     }
 }
 @media only screen and (max-width: 970px) {

   .header-parent span{
       display: flex;
       flex-direction: column;
       padding: 50px;
       align-items: center;
       cursor: pointer;
        position: relative;
       left: 0px; 
     }
   .header-hamburger {
     display: block;
     position: absolute;
     left: 10px;
   }
   .header-parent {
     width: 100%;
     padding: 0 40px;
     position: relative;
   }
   .header-options {
     display: flex;
     flex-direction: column;
     position: absolute;
     left: -100%;
     transition: all 0.8s ease-out;
     top: 0px;
     height: 100vh;
     width: 100%;
     justify-content: space-around;
     background-color: #1f2235;
     z-index: 1000;
     opacity: 0.5;
   }
   .header-option-seperator {
     margin: 0;
   }
   .selected-header-option {
     color: #9ba5e6;
   }
   .header-hamburger-bars {
     font-size: 36px;
     cursor: pointer;
     margin: 0px 10px;
     color: white;
   }
   .show-hamburger-options {
     left: 0;
     opacity: 1;
   }
   .header-logo{
     position: relative;
     left: -5px;
     font-size: 40px;
     padding-left: 20px;
   }
 }